/* HEADER */
.header {
  position: absolute;
  top: 0;
  right: 0;
  left: 0;
  z-index: 9;
  background: transparent;
  will-change: transform;
  backface-visibility: hidden;
  transform-style: preserve-3d;
  transition: all .25s $ease;
  &__wrapper {
    display: flex;
    align-items: center;
    color: $fontColor;
    padding: 32px 0 15px;
    transition: all .25s $ease; }

  &__logo {
    flex: 0 0 auto;
    padding-right: 20px;
    font-size: 0;
    color: $colorPrimary;
    transition: opacity .25s $ease, transform .25s $ease;
    .ico {
      font-size: 48px; }
    &:hover {
      opacity: 0.8; } }

  &__menu {
    flex: 0 1 auto;
    display: flex;
    align-items: center;
    font-size: 13px;
    line-height: 1.3;
    text-transform: uppercase;
    margin: 0 0 0 auto;
    padding: 0;
    li {
      display: inline-block;
      margin-right: 10px;
      &:last-child {
        margin-right: -7px; }
      &.is-active {
        color: $colorPrimary;
        &::after {
          right: 7px; } } }
    a {
      position: relative;
      display: inline-block;
      padding: 7px;
      transition: color .25s $ease;
      &:hover {
        color: $colorPrimary; }
      &:after {
        display: inline-block;
        content: ' ';
        position: absolute;
        bottom: 3px;
        left: 7px;
        right: 101%;
        height: 2px;
        background: $colorPrimary;
        transition: right .25s $ease; }
      &:hover {
        &::after {
          right: 7px; } } } }

  &__hamburger {
    display: none;
    padding-left: 20px; }

  &.is-fixed-visible {
    // visibility: visible
    transform: translate3d(0,0,0) !important; }

  &.is-transitioning {
    transition: transform .25s $ease;
    transform: translate3d(0,-100%,0) !important; }

  &[data-modifier="no-logo"] {
    .header {
      &__logo {
        opacity: 0;
        transform: translateY(-20px);
        pointer-events: none; } } }
  &[data-modifier="is-white"] {
    .header {
      &__logo {
        color: white; }
      &__wrapper {
        color: white; }
      &__menu {
        a {
          &:after {
            background: #fff; }
          &:hover {
            color: rgba(#fff, 0.8); } } }
      &__hamburger {
        .hamburger {
          background: #fff; }
        .hamburger-inner {
          background: $colorPrimary;
          &::before,
          &::after {
            background: $colorPrimary; } } } } }

  &.is-fixed {
    // should be normalized to -100% scroll at this point
    position: fixed;
    // visibility: hidden
    background: #fff;
    transform: translate3d(0,0,0) !important;
    box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.08);
    .header__wrapper {
      padding-top: 15px;
      padding-bottom: 15px; }
    .header__logo {
      opacity: 1;
      transform: translateY(0px);
      pointer-events: all; }
    .header__hamburger {
      .hamburger {
        background: $colorPrimary; }
      .hamburger-inner {
        background: #fff;
        &::before,
        &::after {
          background: #fff; } } }
    &[data-modifier="is-white"] {
      .header {
        &__logo {
          color: $colorPrimary; }
        &__wrapper {
          color: $fontColor; }
        &__menu {
          a {
            &:after {
              background: $colorPrimary; }
            &:hover {
              color: $colorPrimary; } } } } } }
  &.is-active {
    background: transparent;
    box-shadow: none;
    .header__logo {
      opacity: 0;
      transform: translateY(-20px);
      pointer-events: none; }
    .header__hamburger {
      .hamburger {
        background: $colorPrimary; }
      .hamburger-inner {
        background: #fff;
        &::before,
        &::after {
          background: #fff; } } } } }


@include rmin($hd) {
  .header {
    &__wrapper {
      padding: 49px 0 30px; }
    &__menu {
      li {
        margin-right: 25px; }
      a {
        font-size: 16px;
        line-height: 21px; } }
    &__logo {
      .ico {
        font-size: 52px; } } } }


@include r($xl) {
  .header {
    &__logo {
      .ico {
        font-size: 40px; } }
    &__menu {
      font-size: 12px;
      li {
        margin-right: 0px; } } } }

@include r($lg) {
  .header {
    &__menu {
      display: none; }
    &__hamburger {
      margin-left: auto;
      display: block; }
    &__wrapper {
      padding: 16px 0; }
    &.is-fixed {
      .header__wrapper {
        padding: 10px 0; } } } }

@include r($sm) {
  .header {
    // &__wrapper
    //   padding: 20px 0
    &__logo {
      .ico {
        font-size: 34px; } } } }
