/*======================================
 *  Selectric v1.13.0
 *====================================== */

.selectric-wrapper {
  position: relative;
  cursor: pointer; }

.selectric-responsive {
  width: 100%; }

.selectric {
  border: 1px solid $borderColor;
  border-radius: 4px;
  background: transparent;
  position: relative;
  overflow: hidden;
  transition: border-color .25s $ease;
  // z-index: 3
  background: #fff;
  .label {
    display: block;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    margin: 13px 42px 13px 16px;
    font-size: 16px;
    line-height: 21px;
    color: #4F4F4F;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none; }
  .button {
    display: block;
    position: absolute;
    right: 0;
    top: 0;
    width: 42px;
    height: 100%;
    text-align: center;
    transition: all .25s $ease;
    font-size: 0;
    color: transparent;
    &:after {
      content: '';
      position: absolute;
      top: 0;
      right: 0;
      bottom: 0;
      left: 0;
      margin: auto;
      width: 0;
      height: 0;
      width: 8px;
      height: 8px;
      border: 1px solid $fontColor;
      transform: translateY(-2px) rotate(45deg);
      border-top: none;
      border-left: none;
      transition: all .25s $ease; } } }

.selectric-focus .selectric {
  border-color: #aaaaaa; }

.selectric-hover .selectric {
  border-color: #c4c4c4; }

.selectric-above {
  .selectric-items {
    transform: translateY(15px); } }

.selectric-open {
  z-index: 9999;
  &.selectric-above {
    .selectric {
      border-bottom: 1px solid #c4c4c4;
      border-top-color: transparent;
      border-bottom-left-radius: 4px;
      border-bottom-right-radius: 4px;
      border-top-left-radius: 0;
      border-top-right-radius: 0; }
    .selectric-items {
      border-top: 1px solid #c4c4c4;
      border-bottom: none;
      border-bottom-left-radius: 0;
      border-bottom-right-radius: 0;
      border-top-left-radius: 4px;
      border-top-right-radius: 4px;
      transform: translateY(1px); } }
  .selectric {
    border-color: #c4c4c4;
    border-bottom: 1px solid transparent;
    border-bottom-left-radius: 0;
    border-bottom-right-radius: 0; }
  .selectric-items {
    // display: block
    opacity: 1;
    pointer-events: all;
    transform: translateY(-1px);
    border-top: 1px solid transparent;
    border-bottom-left-radius: 4px;
    border-bottom-right-radius: 4px; }
  .button {
    &:after {
      transform: translateY(2px) rotate(-135deg); } } }





.selectric-disabled {
  filter: alpha(opacity = 50);
  opacity: 0.5;
  cursor: default;
  user-select: none; }

.selectric-hide-select {
  position: relative;
  overflow: hidden;
  width: 0;
  height: 0;
  select {
    position: absolute;
    left: -100%; }
  &.selectric-is-native {
    position: absolute;
    width: 100%;
    height: 100%;
    z-index: 10;
    select {
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      height: 100%;
      width: 100%;
      border: none;
      z-index: 1;
      box-sizing: border-box;
      opacity: 0; } } }

.selectric-input {
  position: absolute !important;
  top: 0 !important;
  left: 0 !important;
  overflow: hidden !important;
  clip: rect(0, 0, 0, 0) !important;
  margin: 0 !important;
  padding: 0 !important;
  width: 1px !important;
  height: 1px !important;
  outline: none !important;
  border: none !important;
  *font: 0/0 a !important;
  background: none !important; }

.selectric-temp-show {
  position: absolute !important;
  visibility: hidden !important;
  display: block !important; }

/* Items box */

.selectric-items {
  // display: none
  opacity: 0;
  pointer-events: none;
  position: absolute;
  top: 100%;
  left: 0;
  background: #fff;
  border: 1px solid #c4c4c4;
  z-index: -1;
  transform: translateY(-15px);
  transition: all .25s $ease;
  overflow: hidden;
  box-shadow: 0px 15px 25px rgba(0, 0, 0, 0.1);
  .selectric-scroll {
    height: 100%;
    overflow: auto; } }

.selectric-above .selectric-items {
  top: auto;
  bottom: 100%; }

.selectric-items {
  ul {
    list-style: none;
    padding: 0;
    margin: 0;
    font-size: 12px; }
  li {
    list-style: none;
    padding: 0;
    margin: 0;
    font-size: 14px;
    line-height: 18px;
    display: block;
    padding: 11px 15px;
    color: #4F4F4F;
    cursor: pointer;
    &.selected {
      background: #F9F9F9;
      color: #444; }
    &:hover {
      background: #F9F9F9; } }
  .disabled {
    filter: alpha(opacity = 50);
    opacity: 0.5;
    cursor: default !important;
    background: none !important;
    color: #666 !important;
    user-select: none; }
  .selectric-group {
    .selectric-group-label {
      font-weight: bold;
      padding-left: 10px;
      cursor: default;
      user-select: none;
      background: none;
      color: #444; }
    &.disabled li {
      filter: alpha(opacity = 100);
      opacity: 1; }
    li {
      padding-left: 25px; } } }
