.hamburger {
  padding: 8px;
  display: inline-block;
  cursor: pointer;
  transition-property: background, filter;
  transition-duration: 0.15s;
  transition-timing-function: linear;
  font: inherit;
  color: inherit;
  text-transform: none;
  background-color: transparent;
  border: 0;
  background: #0BA14B;
  border-radius: 3px;
  overflow: visible;
}

.hamburger:hover {
  background: #098940;
}

.hamburger-box {
  width: 18px;
  height: 12px;
  display: inline-block;
  position: relative;
  transform: translateY(-1px);
}

.is-maclike .hamburger-box {
  transform: none;
