/* Checkbox */
.ui-checkbox input {
  display: none;
  width: 0.1px;
  height: 0.1px;
  opacity: 0;
  position: absolute;
  top: 0;
  left: 0;
}

.ui-checkbox input:checked + span {
  background: #0BA14B;
}

.ui-checkbox input:checked + span::after {
  transform: translateY(-50%) scale(1) rotate(-45deg);
}

.ui-checkbox label {
  display: block;
  width: 100%;
  position: relative;
  cursor: pointer;
  padding-left: 38px;
  font-size: 14px;
  line-height: 19px;
}

.ui-checkbox span {
  position: absolute;
  left: 0;
  top: 50%;
  transform: translateY(-50%);
  display: inline-block;
  size: 22px;
  background: #E0E0E0;
  border-radius: 3px;
  cursor: pointer;
  vertical-align: middle;
  cursor: pointer;
  transition: all 0.25s ease-in-out;
}

.ui-checkbox span::after {
  position: absolute;
  left: 7px;
  top: 48%;
  transform: translateY(-50%) scale(0) rotate(-45deg);
  display: inline-block;
  content: ' ';
  width: 7px;
  height: 4px;
  border: 2px solid #fff;
  border-right: none;
  border-top: 0;
  transition: all 0.25s ease-in-out;
}

