/* HEADER */
.header {
  position: absolute;
  top: 0;
  right: 0;
  left: 0;
  z-index: 9;
  background: transparent;
  will-change: transform;
  backface-visibility: hidden;
  transform-style: preserve-3d;
  transition: all 0.25s ease-in-out;
}

.header__wrapper {
  display: flex;
  align-items: center;
  color: #202020;
  padding: 32px 0 15px;
  transition: all 0.25s ease-in-out;
}

.header__logo {
  flex: 0 0 auto;
  padding-right: 20px;
  font-size: 0;
  color: #0BA14B;
  transition: opacity 0.25s ease-in-out, transform 0.25s ease-in-out;
}

.header__logo .ico {
  font-size: 48px;
}

.header__logo:hover {
  opacity: 0.8;
}

.header__menu {
  flex: 0 1 auto;
  display: flex;
  align-items: center;
  font-size: 13px;
  line-height: 1.3;
  text-transform: uppercase;
  margin: 0 0 0 auto;
  padding: 0;
}

.header__menu li {
  display: inline-block;
  margin-right: 10px;
}

.header__menu li:last-child {
  margin-right: -7px;
}

.header__menu li.is-active {
  color: #0BA14B;
}

.header__menu li.is-active::after {
  right: 7px;
}

.header__menu a {
  position: relative;
  display: inline-block;
  padding: 7px;
  transition: color 0.25s ease-in-out;
}

.header__menu a:hover {
  color: #0BA14B;
}

.header__menu a:after {
  display: inline-block;
  content: ' ';
  position: absolute;
  bottom: 3px;
  left: 7px;
  right: 101%;
  height: 2px;
  background: #0BA14B;
  transition: right 0.25s ease-in-out;
}

.header__menu a:hover::after {
  right: 7px;
}

.header__hamburger {
  display: none;
  padding-left: 20px;
}

.header.is-fixed-visible {
  transform: translate3d(0, 0, 0) !important;
}

.header.is-transitioning {
  transition: transform 0.25s ease-in-out;
  transform: translate3d(0, -100%, 0) !important;
}

.header[data-modifier="no-logo"] .header__logo {
  opacity: 0;
  transform: translateY(-20px);
  pointer-events: none;
}

.header[data-modifier="is-white"] .header__logo {
  color: white;
}

.header[data-modifier="is-white"] .header__wrapper {
  color: white;
}

.header[data-modifier="is-white"] .header__menu a:after {
  background: #fff;
}

.header[data-modifier="is-white"] .header__menu a:hover {
  color: rgba(255, 255, 255, 0.8);
}

.header[data-modifier="is-white"] .header__hamburger .hamburger {
  background: #fff;
}

.header[data-modifier="is-white"] .header__hamburger .hamburger-inner {
  background: #0BA14B;
}

.header[data-modifier="is-white"] .header__hamburger .hamburger-inner::before, .header[data-modifier="is-white"] .header__hamburger .hamburger-inner::after {
  background: #0BA14B;
}

.header.is-fixed {
  position: fixed;
  background: #fff;
  transform: translate3d(0, 0, 0) !important;
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.08);
}

.header.is-fixed .header__wrapper {
  padding-top: 15px;
  padding-bottom: 15px;
}

.header.is-fixed .header__logo {
  opacity: 1;
  transform: translateY(0px);
  pointer-events: all;
}

.header.is-fixed .header__hamburger .hamburger {
  background: #0BA14B;
}

.header.is-fixed .header__hamburger .hamburger-inner {
  background: #fff;
}

.header.is-fixed .header__hamburger .hamburger-inner::before, .header.is-fixed .header__hamburger .hamburger-inner::after {
  background: #fff;
}

.header.is-fixed[data-modifier="is-white"] .header__logo {
  color: #0BA14B;
}

.header.is-fixed[data-modifier="is-white"] .header__wrapper {
  color: #202020;
}

.header.is-fixed[data-modifier="is-white"] .header__menu a:after {
  background: #0BA14B;
}

.header.is-fixed[data-modifier="is-white"] .header__menu a:hover {
  color: #0BA14B;
}

.header.is-active {
  background: transparent;
  box-shadow: none;
}

.header.is-active .header__logo {
  opacity: 0;
  transform: translateY(-20px);
  pointer-events: none;
}

.header.is-active .header__hamburger .hamburger {
  background: #0BA14B;
}

.header.is-active .header__hamburger .hamburger-inner {
  background: #fff;
}

.header.is-active .header__hamburger .hamburger-inner::before, .header.is-active .header__hamburger .hamburger-inner::after {
  background: #fff;
}

