.history-nav {
  left: 0;
  position: relative;
  background: #fff;
  box-shadow: 0px -5px 9px rgba(0, 0, 0, 0.1);
  width: 100% !important;
  z-index: 5;
  transform: translateY(-150px);
  top: 82px !important;
  transition: all 0.25s ease-in-out;
  opacity: 0;
}

.history-nav.is_stuck {
  transform: translateY(0);
  opacity: 1;
}

.history-nav__overflow {
  overflow: hidden;
  height: 65px;
}

.history-nav__list {
  overflow: auto;
  padding-top: 17px;
  padding-bottom: 50px;
  display: flex;
  flex-wrap: nowrap;
  justify-content: space-between;
  position: relative;
}

.history-nav__list:before {
  position: absolute;
  display: block;
  content: '';
  width: 100%;
  height: 5px;
  background: #E0E0E0;
  border-radius: 20px;
  left: 0;
  top: 45px;
}

.history-nav__list a {
  font-size: 14px;
  line-height: 1.3;
  color: #202020;
  padding: 5px 15px 10px;
  position: relative;
  display: inline-block;
  white-space: nowrap;
}

.history-nav__list a::after {
  position: absolute;
  display: block;
  content: '';
  width: 100%;
  border-radius: 20px;
  height: 5px;
  background: #0BA14B;
  left: 0;
  bottom: 0;
  transition: all 0.25s ease-in-out;
  opacity: 0;
  z-index: 2;
}

.history-nav__list a.is-active::after {
  opacity: 1;
}

@media only screen and (max-width: 1500px) {
  .history-nav {
    top: 78px !important;
  }
}

