  .history-nav {
    top: 60px !important;
  }
  .history-nav__overflow {
    margin: 0 -24px;
    width: calc(100% + 48px);
  }
  .history-nav__list::before {
    position: fixed;
    left: 24px;
    width: calc(100% - 48px);
  }
  .history-nav__list::after {
    display: block;
    position: relative;
    content: 's';
    font-size: 1px;
    color: transparent;
  }
  .history-nav__list a::before {
    position: absolute;
    display: block;
    content: '';
    width: 100%;
    border-radius: 0px;
    height: 5px;
    background: #E0E0E0;
    left: 0;
    bottom: 0;
    transition: all 0.25s ease-in-out;
    opacity: 1;
    z-index: 2;
  }
  .history-nav__list a:first-child {
    margin-left: 24px;
  }
  .history-nav__list a:first-child::before {
    border-top-left-radius: 20px;
    border-bottom-left-radius: 20px;
  }
  .history-nav__list a:last-child {
    margin-right: 24px;
  }
  .history-nav__list a:last-child::before {
    border-top-right-radius: 20px;
    border-bottom-right-radius: 20px;
  }
}

@media only screen and (max-width: 576px) {
